/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/no-array-index-key */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/jsx-indent */
import React from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'react-bootstrap';
import slugify from 'slugify';

import ItemTile from './ItemTile';

function Education({
  description,
  name,
  location,
  timePeriod,
  programme,
  qa,
}) {
  return (
      <ItemTile qa={qa} className="education">
        <Row id={slugify(`${qa}-education`)}>
          <Col md={3} sm={3}>
            <div id={slugify(`${qa}-name`)} className="institution">{name}</div>
            <div id={slugify(`${qa}-timeperiod`)} className="timeperiod">{timePeriod}</div>
            <div id={slugify(`${qa}-location`)} className="location">{location}</div>
          </Col>
          <Col md={9} sm={9}>
            <div id={slugify(`${qa}-programme`)} className="programme"><b>{programme}</b></div>
            <div id={slugify(`${qa}-description`)} className="description">{description}</div>
          </Col>
        </Row>
      </ItemTile>
  );
}

Education.propTypes = {
  description: PropTypes.string,
  name: PropTypes.string.isRequired,
  location: PropTypes.string,
  timePeriod: PropTypes.string.isRequired,
  programme: PropTypes.string,
  qa: PropTypes.string,
};

Education.defaultProps = {
  description: '',
  location: '',
  programme: '',
  qa: 'education-block',
};

export default Education;



