import slugify from 'slugify';
import React from 'react';
import { Image } from 'react-bootstrap';
import PropTypes from 'prop-types';

function Img({ className, imgUrl, qa }) {
  return (
    <Image
      id={slugify(`${qa}-img`)}
      src={imgUrl}
      className={className}
      circle
      responsive
    />
  );
}

Img.propTypes = {
  className: PropTypes.string,
  imgUrl: PropTypes.string.isRequired,
  qa: PropTypes.string,
};

Img.defaultProps = {
  className: null,
  qa: 'profile-img',
};

export default Img;



