import _ from 'lodash';
import React, { Component } from 'react';
import { ButtonToolbar, ToggleButton, ToggleButtonGroup } from 'react-bootstrap';
import PropTypes from 'prop-types';
import slugify from 'slugify';

class Skills extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedSkills: {},
    };
  }

  onGroupChange(categoryName, changedSkills) {
    const currentSkills = _.cloneDeep(this.state.selectedSkills);
    currentSkills[categoryName] = changedSkills;
    this.setState({
      selectedSkills: currentSkills,
    });
    this.props.onChange(categoryName, changedSkills);
  }

  getSkillsForCategory(categoryName, skills, categoryId) {
    return (
      <ToggleButtonGroup
        onChange={changedSkills => this.onGroupChange(categoryName, changedSkills)}
        type="checkbox"
        name="wazze"
        key={categoryId}
        id={categoryId}
        vertical
      >
        <h4>{categoryName}</h4>
        {skills.map((skillName) => {
                const skillId = slugify(`${categoryId}-${skillName}`).toLowerCase();
                return (
                  <ToggleButton
                    key={skillId}
                    id={skillId}
                    value={skillName}
                    // onChange={skill => console.log(skill)}
                  >
                    {skillName}
                  </ToggleButton>);
            })}
      </ToggleButtonGroup>
    );
  }

  render() {
    const {
      skillsByCategories, qa,
    } = this.props;

    return (
      <ButtonToolbar>
        {skillsByCategories.map(({ name, skills }) => {
                    const categoryId = slugify(`${qa}-${name}`).toLowerCase();
                    return this.getSkillsForCategory(name, skills, categoryId);
                })
                }
      </ButtonToolbar>);
  }
}

Skills.propTypes = {
  maxSkillsAllowed: PropTypes.number,
  onChange: PropTypes.func.isRequired,
  skillsByCategories: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    skills: PropTypes.arrayOf(PropTypes.string),
  })).isRequired,
  qa: PropTypes.string,
};

Skills.defaultProps = {
  maxSkillsAllowed: 1,
  qa: 'skills',
};

export default Skills;



