/* eslint-disable react/no-array-index-key */
/* eslint-disable react/jsx-closing-tag-location */
import _ from 'lodash';
import slugify from 'slugify';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { Grid, Col, Row } from 'react-bootstrap';

import BlockTile from './components/BlockTile';
import Education from './components/Education';
import Experience from './components/Experience';
import Language from './components/Language';
import ProfileBlock from './components/ProfileBlock';
import UrlsBlock from './components/UrlsBlock';
import Skills from './components/Skills';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.onChange = this.addCategorySkillsToState.bind(this);
    this.state = {
      selectedSkills: {},
    };
  }

  // eslint-disable-next-line class-methods-use-this
  getBlockData(data, selectedSkills) {
    const areSkillsMatching = (skills, desiredSkills) => {
      const matchingSkills = _.intersection(skills, desiredSkills);
      return _.isEmpty(desiredSkills) ? true : matchingSkills.length > 0;
    };

    const skillsWithoutCategory = _.flatten(Object.values(selectedSkills));
    let filteredData = data;
    if (!_.isEmpty(skillsWithoutCategory)) {
      filteredData = filteredData.filter(({ skills }) =>
        areSkillsMatching(skills, skillsWithoutCategory))
        .map((obj) => {
          const copiedObj = _.cloneDeep(obj);
          const { projects } = copiedObj;
          copiedObj.projects = projects ?
            projects.filter(({ skills }) =>
              areSkillsMatching(skills, skillsWithoutCategory)) : projects;
          return copiedObj;
        });
    }

    return filteredData;
  }

  addCategorySkillsToState(categoryName, skills) {
    const currentSkills = _.cloneDeep(this.state.selectedSkills);
    currentSkills[categoryName] = skills;
    this.setState({
      selectedSkills: currentSkills,
    });
  }

  // eslint-disable-next-line class-methods-use-this
  renderExperiences(selectedSkills, experienceData, qaName) {
    const selectedSkillsFlattened = _.flatten(Object.values(selectedSkills));
    const experiences = this.getBlockData(experienceData, selectedSkills)
      .map((expObj) => {
        const copiedObj = _.cloneDeep(expObj);
        copiedObj.forceDecollapse = selectedSkillsFlattened.length > 0;
        return copiedObj;
      });
    return experiences.map((content, index) =>
      (<Experience
        qa={slugify(`${this.props.qa}-${qaName}-${index}`)}
        key={`${qaName}-${index}`}
        {...content}
      />));
  }

  renderEducationItems(selectedSkills) {
    const educationItems = this.getBlockData(this.props.education, selectedSkills);
    return educationItems.map((content, index) =>
      (<Education
        qa={slugify(`${this.props.qa}-education-${index}`)}
        key={`education-${index}`}
        {...content}
      />));
  }

  render() {
    const {
      education, experience, organizationalExperience,
      profileImgUrl, languages, name, skillsByCategories, urls, qa,
    } = this.props;
    return (
      <div className="container">
        <Grid>
          <Row>
            {/* Left block */}
            <Col xs={12} sm={3}>

              {/* Profile image */}
              <BlockTile className="profile-info" qa={slugify(`${qa}-profile-img-tile`)}>
                <ProfileBlock
                  profileImgUrl={profileImgUrl}
                  name={name}
                  qa={slugify(`${qa}-profile`)}
                />
              </BlockTile>

              {/* Urls */}
              {urls && <BlockTile qa={slugify(`${qa}-urls-tile`)} title="Links" className="urls">
                <UrlsBlock urls={urls} qa={slugify(`${qa}-urls`)} />
                </BlockTile>}

              {/* Skills */}
              {skillsByCategories && <BlockTile qa={slugify(`${qa}-skills-tile`)} title="Skills" className="skills">
                <Skills
                  skillsByCategories={skillsByCategories}
                  onChange={this.onChange}
                  isChecked={this.isChecked}
                  qa={slugify(`${qa}-skills`)}
                />
              </BlockTile>}

            </Col>

            {/* Right block */}
            <Col xs={12} sm={9}>

              {/* Experience */}
              {experience && <BlockTile title="Experience" qa={slugify(`${qa}-experience-tile`)}>
                {this.renderExperiences(this.state.selectedSkills, experience, 'experience')}
              </BlockTile>}

              {/* Education */}
              {education && <BlockTile title="Education" qa={slugify(`${qa}-education-tile`)}>
                {this.renderEducationItems(this.state.selectedSkills)}
              </BlockTile>}

              {/* Organizational experience */}
              {organizationalExperience && <BlockTile title="Organizational experience" qa={slugify(`${qa}-experience-tile`)}>
                {this.renderExperiences(this.state.selectedSkills, organizationalExperience, 'org-experience')}
              </BlockTile>}

              {/* Language */}
              {languages && <BlockTile title="Languages" qa={slugify(`${qa}-language-tile`)}>
                {languages.map((data, index) =>
                      (<Language
                        {...data}
                        qa={slugify(`${qa}-language-${index}`)}
                        key={`language-${index}`}
                      />))
                  }
              </BlockTile>}
            </Col>

          </Row>
        </Grid>
      </div>
    );
  }
}

App.propTypes = {
  education: PropTypes.arrayOf(PropTypes.shape({
    description: PropTypes.string,
    name: PropTypes.string.isRequired,
    location: PropTypes.string,
    forceDecollapse: PropTypes.bool,
    timePeriod: PropTypes.string.isRequired,
    programme: PropTypes.string,
  })),
  experience: PropTypes.arrayOf(PropTypes.shape({
    description: PropTypes.string,
    employer: PropTypes.string.isRequired,
    forceDecollapse: PropTypes.bool,
    timePeriod: PropTypes.string.isRequired,
    position: PropTypes.string.isRequired,
    projects: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string,
      description: PropTypes.string,
    })),
    skills: PropTypes.arrayOf(PropTypes.string),
  })),
  organizationalExperience: PropTypes.arrayOf(PropTypes.shape({
    description: PropTypes.string,
    employer: PropTypes.string.isRequired,
    forceDecollapse: PropTypes.bool,
    timePeriod: PropTypes.string.isRequired,
    position: PropTypes.string.isRequired,
    projects: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string,
      description: PropTypes.string,
    })),
    skills: PropTypes.arrayOf(PropTypes.string),
  })),
  profileImgUrl: PropTypes.string.isRequired,
  languages: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    level: PropTypes.string.isRequired,
    certificate: PropTypes.string,
  })),
  name: PropTypes.string.isRequired,
  skillsByCategories: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    skills: PropTypes.arrayOf(PropTypes.string),
  })),
  urls: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    imgUrl: PropTypes.string,
    targetUrl: PropTypes.string,
  })),
  qa: PropTypes.string,
};

App.defaultProps = {
  education: [],
  experience: [],
  organizationalExperience: [],
  languages: [],
  skillsByCategories: [],
  urls: [],
  qa: 'cv-app',
};

export default App;



