/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/no-array-index-key */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/jsx-indent */
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Collapse, Glyphicon, Row } from 'react-bootstrap';
import slugify from 'slugify';
import ItemTile from './ItemTile';

class Experience extends Component {
  constructor(props) {
    super(props);
    const collapsedProjects = {};
    const currentProjects = props.projects;
    if (currentProjects) {
      currentProjects.forEach((obj, index) => {
        collapsedProjects[`project-${index}`] = false;
      });
    }
    this.state = {
      collapsedProjects,
    };
  }

  componentWillReceiveProps(nextProps) {
    const currentProjects = nextProps.projects;
    const collapsedProjects = {};
    if (currentProjects) {
      currentProjects.forEach((obj, index) => {
        collapsedProjects[`project-${index}`] = !!nextProps.forceDecollapse;
      });
    }
    this.setState({
      collapsedProjects,
    });
  }

  collapseProject(projectIndex) {
    const projectKey = `project-${projectIndex}`;
    const newState = _.cloneDeep(this.state);
    const projectCurrentState = newState.collapsedProjects[projectKey];
    newState.collapsedProjects[projectKey] = !projectCurrentState;
    this.setState(newState);
  }

  render() {
    const {
      employer,
      location,
      timePeriod,
      position,
      description,
      projects,
      qa,
    } = this.props;
    return (
      <ItemTile id={qa} className="experience">
        <Row id={slugify(`${qa}-experience`)}>
          <Col md={3} sm={3}>
            <div id={slugify(`${qa}-employer`)} className="institution">{employer}</div>
            <div id={slugify(`${qa}-timeperiod`)} className="timeperiod">{timePeriod}</div>
              {location && <div id={slugify(`${qa}-location`)} className="location">{location}</div>}
          </Col>
          <Col md={9} sm={9}>
            <div id={slugify(`${qa}-position`)} className="position"><b>{position}</b></div>
            <div id={slugify(`${qa}-description`)} className="description">{description}</div>
            { projects &&
              <div id={slugify(`${qa}-projects`)}>
                  {projects.map(({ name, description: projectDescription }, index) =>
                                (<div className="project" key={index} id={slugify(`${qa}-project-${index}`)}>
                                  <div
                                    id={slugify(`${qa}-project-${index}-name`)}
                                    onClick={() => this.collapseProject(index)}
                                  >
                                    <b>Project:</b> {name} <Glyphicon
                                      glyph={this.state.collapsedProjects[`project-${index}`] ? 'chevron-up' : 'chevron-down'}
                                    />
                                  </div>
                                  <Collapse in={this.state.collapsedProjects[`project-${index}`]}>
                                    <div id={slugify(`${qa}-project-${index}-description`)}>
                                      {projectDescription}
                                    </div>
                                  </Collapse>
                                 </div>))}
              </div>
            }
          </Col>
        </Row>
      </ItemTile>
    );
  }
}

Experience.propTypes = {
  description: PropTypes.string,
  employer: PropTypes.string.isRequired,
  forceDecollapse: PropTypes.bool,
  location: PropTypes.string,
  timePeriod: PropTypes.string.isRequired,
  position: PropTypes.string.isRequired,
  projects: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
  })),
  qa: PropTypes.string,
};

Experience.defaultProps = {
  description: '',
  forceDecollapse: false,
  location: undefined,
  projects: undefined,
  qa: 'experience-block',
};

export default Experience;



