import React from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'react-bootstrap';
import slugify from 'slugify';
import ItemTile from './ItemTile';

function Language({
  name, level, certificate, qa,
}) {
  return (
    <ItemTile id={qa}>
      <Row id={slugify(`${qa}-language`)}>
        <Col xs={3}>
          <div id={slugify(`${qa}-name`)}>{name}</div>
        </Col>
        <Col xs={9}>
          <div id={slugify(`${qa}-level`)}><b>{level}</b></div>
          {certificate && <div id={slugify(`${qa}-certificate`)}>{certificate}</div>}
        </Col>
      </Row>
    </ItemTile>
  );
}

Language.propTypes = {
  certificate: PropTypes.string,
  level: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  qa: PropTypes.string,
};

Language.defaultProps = {
  certificate: null,
  qa: 'language-block',
};

export default Language;



