/* eslint-disable react/jsx-closing-tag-location */
/* eslint-disable react/no-array-index-key */
import React from 'react';
import PropTypes from 'prop-types';
import slugify from 'slugify';
import Img from './Img';

function UrlsBlock({
  qa, urls,
}) {
  return (
    <div className="profile-urls">
      {urls && urls.map(({ imgUrl, name: urlName, targetUrl }, index) =>
                    (<div key={`url-${index}`}>
                      <a href={targetUrl} id={slugify(`${qa}-url-${index}`)}>
                        <Img
                          className="url-img"
                          key={`url-${index}`}
                          qa={slugify(`${qa}-url-${index}`)}
                          imgUrl={imgUrl}
                        />
                        {urlName}
                      </a>
                    </div>))
                }
    </div>
  );
}

UrlsBlock.propTypes = {
  qa: PropTypes.string,
  urls: PropTypes.arrayOf(PropTypes.shape({
    imgUrl: PropTypes.string,
    name: PropTypes.string,
    targetUrl: PropTypes.string,
  })),
};

UrlsBlock.defaultProps = {
  qa: 'urls',
  urls: undefined,
};

export default UrlsBlock;



